import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-userdash-events-comp',
  templateUrl: './userdash-events-comp.component.html',
  styleUrls: ['./userdash-events-comp.component.scss']
})
export class UserdashEventsCompComponent implements OnInit {

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  meetingsEventData:any;
  facilityEventData:any;

  constructor() { }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  ngOnInit() {

  	this.meetingsEventData = [
	  	{
	  		'date': '24/11/2019',
	  		'time':'5:00-6:00 PM',
	  		'subject': 'Annual General Meeting'
	  	},
	  	{
	  		'date': '23/12/2019',
	  		'time':'4:00-6:00 PM',
	  		'subject': 'Annual General Meeting'
	  	}
  	];

  	this.facilityEventData = [
	  	{
	  		'date': '24/11/2019',
	  		'time':'5:00-6:00 PM',
	  		'subject': 'Party Hall'
	  	},
	  	{
	  		'date': '23/12/2019',
	  		'time':'4:00-6:00 PM',
	  		'subject': 'Tennis Court'
	  	}
  	]

  }

}
